import React, { Fragment } from 'react';
import Head from '../components/head';
import './skills.css'
import SkillItem from '../components/skillItem';
const Skills = () => {
    const languages = [
        'Javascript',
        'Java',
        'Solidity',
        'Php'
    ];

    const frameworks = [
        'Nodejs/Express',
        'React/React native',
        'Laravel',
        'Ethereum/Truffle'
    ];

    const paradigms = [
        'OBJECT-ORIENTED PROGRAMMING(OOP)',
        'ASYNCHRONOUS PROGRAMMING',
        'BLOCKCHAIN-DECENTRALIZED APPS',
        'REST & graphQL APIS',
    ];

    const dataStorage = [
        'mysql',
        'mongoDB'
    ];
    return (
        <Fragment>
            <Head />
            <div className="skills-list">
                <SkillItem title="Programming Languages" info={languages} />
                <SkillItem title="Frameworks" info={frameworks} />
                <SkillItem title="Paradigms" info={paradigms} />
                <SkillItem title="Data Storage" info={dataStorage} />
            </div>
        </Fragment>
    );
}

export default Skills;

